port { container } from '../../../../../domain/core/diContainer';\nimport { IIDEInputDefinitions } from '../../../../../domain/presentation/IIDEInputDefinitions';\nimport { IOrganizationUseCase } from '../../../../../domain/useCase/IOrganizationUseCase';\nimport { IUseCaseExecutor } from '../../../../../domain/useCase/IUseCaseExecutor';\nimport IDEInput from \"../../../components/application/IDEInput.vue\";\n\nconst emit = defineEmits([\"close\"]);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst organization = container.resolve<IOrganizationUseCase>(IOrganizationUseCase);\nconst organizationName = ref(\"\");\nconst isOrgNameValid = ref<boolean>(false);\nconst props = defineProps<{ onOrganizationCreated: (createdOrg: IOrganization) => void }>();\nconst validations = ref<Record<\"organizationName\", IIDEInputDefinitions[\"validations\"]>>({\n    organizationName: { minAndMaxLength: { min: 3, max: 25 }, availableCharacters: true, nonSpaceCharacter: true },\n});\n\nfunction close() {\n    emit(\"close\", \"close\");\n}\nfunction createOrganization() {\n    executor.execute(async () => {\n        const createdOrg = await organization.createOrganization(organizationName.value, {});\n        props.onOrganizationCreated?.(createdOrg);\n    }, { loading: true, noContinueInBgBtn: true, loadingInfo: { title: \"We're working hard behind the scenes to build your organization.\", text: \"In the meantime, did you know that the average person spends four months of their lifetime waiting for red lights to turn green? But don't worry, you won't have to wait that long. We're working to get things up and running as quickly as possible.\" } });\n    close();\n}\nfunction orgNameValidationStatus(isValid: boolean) {\n    isOrgNameValid.value = isValid\n\n}\nfunction setNameValue(value: string) {\n    organizationName.value = value\n}\nconst createButtonDisabled = computed(() => !isOrgNameValid.value || organizationName.value.length == 0);\n</script>\n<style scoped>\n* {\n    font-family: 'Roboto';\n}\n\n.container {\n    padding: 32px 20px 20px;\n}\n\n.organization-name-div {\n    margin-bottom: 52px\n}\n\n.name-label {\n    font-weight: 500;\n    font-size: 15px;\n    line-height: 18px;\n    color: #151233;\n    margin-bottom: 4px;\n}\n\n.organizaton-name {\n    border: 1px solid #BCBCBC;\n    border-radius: 4px;\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    padding: 8px 8px 8px 12px;\n    width: 100%;\n    font-size: 15px;\n    color: #212121;\n    outline: none;\n}\n\n.organizaton-name::placeholder {\n    color: #BCBCBC\n}\n\n.buttons-div {\n    display: flex;\n    justify-content: end;\n\n}\n\n.cancel-button {\n    font-weight: 500;\n    font-size: 16px;\n    text-align: center;\n    color: #757575;\n    display: flex;\n    align-items: center;\n    padding: 8px 16px;\n    border-radius: 4px;\n    margin-right: 4px;\n    border: none;\n    background: transparent;\n}\n\n.create-button {\n    font-weight: 500;\n    font-size: 16px;\n    text-align: center;\n    color: #FFFFFF;\n    background: #151233;\n    display: flex;\n    align-items: center;\n    padding: 8px 16px;\n    border-radius: 4px;\n    border: none;\n}\n\n.create-button:disabled {\n    color: #757575\n}\n\n.button-tooltip {\n    visibility: hidden;\n    position: absolute;\n    background: #757575;\n    color: #FFFFFF;\n    font-size: 11px;\n    font-family: 'Roboto';\n    text-align: center;\n    border-radius: 4px;\n    letter-spacing: 0.2px;\n    padding: 6px 8px 6px 8px;\n    top: 56px;\n    left: 63px;\n    opacity: 0;\n}\n\n.create-button:disabled:hover+.button-tooltip {\n    visibility: visible;\n    transition: opacity 0.3s ease-out 0.5s;\n    opacity: 1;\n}\n</style>"],"sourceRoot":""}]);
